$blue-card-primary: #4ba3c9;
$blue-card-secondary: darken($blue-card-primary, 5%);
$blue-card-flap: darken($blue-card-primary, 8%);

$red-card-primary: #773139;
$red-card-secondary: darken($red-card-primary, 5%);
$red-card-flap: darken($red-card-primary, 8%);

$card-border-radius: 6px;
$card-width: 280px;
$card-height: 250px;

.card {
  width: $card-width;
  height: $card-height;
  border-bottom-left-radius: $card-border-radius;
  border-bottom-right-radius: $card-border-radius;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.card.blue {
  background-color: $blue-card-flap;
}
.card.red {
  background-color: $red-card-flap;
}
.front {
  position: absolute;
  width: 0;
  height: 0;
  z-index: 3;
}
.flap {
  border-left: ($card-width / 2) solid transparent;
  border-right: ($card-width / 2) solid transparent;
  border-bottom: ($card-height / 2) - 8 solid transparent;
  transform-origin: top;
  pointer-events: none;
}
.flap.blue {
  border-top: ($card-height / 2) + 8 solid $blue-card-flap;
}
.flap.red {
  border-top: ($card-height / 2) + 8 solid $red-card-flap;
}
.pocket {
  border-top: ($card-height / 2) solid transparent;
  border-bottom-left-radius: $card-border-radius;
  border-bottom-right-radius: $card-border-radius;
}
.pocket.blue {
  border-left: ($card-width / 2) solid $blue-card-primary;
  border-right: ($card-width / 2) solid $blue-card-primary;
  border-bottom: ($card-height / 2) solid $blue-card-secondary;
}
.pocket.red {
  border-left: ($card-width / 2) solid $red-card-primary;
  border-right: ($card-width / 2) solid $red-card-primary;
  border-bottom: ($card-height / 2) solid $red-card-secondary;
}
