.gb-background {
  width: 100%;
  height: auto;
  background-color: #1e1c1c;

  .gb-game-checkpoint {
    font-family: Digital, monospace;
    font-size: min(max(14px, 7vw), 20px);
    color: #d7213c;
    padding: 10px;
    text-align: center;
  }

  .gb-game-checkpoint.padding-top {
    padding-top: 100px;
  }

  .gb-game-checkpoint.padding-bottom {
    padding-bottom: 100px;
  }

  .gb-bridge-container {
    width: 30%;
    height: auto;
    margin: 0 auto;
    border-left: #814949 10px solid;
    border-right: #814949 10px solid;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;

    .gb-bridge-divider {
      width: 45%;
      height: auto;
    }

    .gb-bridge-level {
      width: 100%;
      height: 100px;
      border-top: #814949 2px solid;
      border-bottom: #814949 2px solid;
      margin-bottom: 30px;
      background: grey;
      opacity: 0.7;

      .bulbs {
        width: 10px;
        height: 10px;
        background: yellow;
        margin: 8px 0 10px;
        border-radius: 10px;
      }
    }
    .gb-bridge-level:last-child {
      margin-bottom: 0;
    }

    .left {
      border-right: #814949 2px solid;
      .bulbs {
        margin-left: -10px;
      }
    }
    .right {
      border-left: #814949 2px solid;
      .bulbs {
        position: relative;
        right: -100%;
      }
    }
    .active {
      opacity: 0.2;
      cursor: pointer;
    }
    .safe {
      animation: greenBlinkingTile 4s;
    }
    .unsafe {
      animation: redBlinkingTile 4s;
    }

    @keyframes greenBlinkingTile {
      0% {
        background-color: #10c018;
      }
      25% {
        background-color: white;
      }
      50% {
        background-color: #10c018;
      }
      75% {
        background-color: white;
      }
    }

    @keyframes redBlinkingTile {
      0% {
        background-color: red;
      }
      25% {
        background-color: white;
      }
      50% {
        background-color: red;
      }
      75% {
        background-color: white;
      }
    }
  }
  @media only screen and (max-width: 992px) {
    .gb-bridge-container {
      width: 50%;
    }
  }
}
