.modal-wrapper {
  position: fixed;
  display: flex;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  z-index: 11000;

  svg.background {
    position: absolute;
    top: 10%;
    left: 10%;
    opacity: 0.15;
    width: 80%;
    height: 80%;
    pointer-events: none;
  }

  svg.logo {
    opacity: 0.15;
    width: 30%;
    pointer-events: none;
    text-align: center;
  }

  .content-wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    max-width: 700px;
    width: 90%;
    background: black;
    color: white;
    z-index: 50;
    text-align: center;
    font-family: 'Quicksand', sans-serif;
    font-weight: 400;
    border-radius: 20px;
    box-shadow: 0 0 20px 2px #00000059;
    padding: 20px 60px;
    max-height: 90vh;
    overflow: auto;
    box-sizing: border-box;
    pointer-events: auto;
    transform: translate(-50%, -50%);

    .content-inner {
      padding: 30px 10px;
    }
  }
}

.grey-bg {
  background: rgba(51, 51, 51, 0.5);
}
