.card-wrapper {
  width: auto;
  display: flex;
  margin: 25px;
  cursor: pointer;

  svg.logo {
    opacity: 0.15;
    width: 30%;
    pointer-events: none;
    text-align: center;
  }

  .content-wrapper {
    width: 400px;
    height: 280px;
    background: black;
    color: white;
    text-align: center;
    font-family: 'Quicksand', sans-serif;
    font-weight: 400;
    border-radius: 20px;
    box-shadow: 0 0 20px 2px #00000059;
    padding: 20px 60px;
    overflow: auto;
    pointer-events: auto;
    box-sizing: border-box;

    .content-inner {
      padding: 28% 10px;
      font-size: 18px;
    }
  }
}
