.App {
  text-align: center;
}

.squid-game-bg {
  width: 100%;
  height: 100vh;
  background-image: url(./assets/squid_bg.jpg);
  background-size: cover;
}

.card-container {
  display: flex;
  justify-content: center;
}
.card-holder {
  margin: 10px;
}

.counter {
  font-family: Digital, monospace;
  font-size: min(max(14px, 7vw), 40px);
  color: #d7213c;
  border: 5px outset #8b8b8b;
  background: black;
  text-align: center;
  padding: 0 20px;
  position: absolute;
}
.slap {
  top: 4vw;
  left: 1.5vw;
}
.won {
  top: 4vw;
  right: 1.5vw;
}

@media only screen and (max-width: 992px) {
  .slap,
  .won {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
  }
  .won {
    top: 15vw;
  }
}
