.App {
  text-align: center;
}

.gamecards-wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  padding: 20px;
  padding-top: 50px;
  background-image: url(./assets/squid_bg.jpg);
  background-size: cover;
}

.quit-game {
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 111111;
  color: white;
  background: black;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 5px;
}

.quit-game.light {
  background: #bbbbbb;
  color: black;
}

@media only screen and (max-width: 700px) {
  .gamecards-wrapper {
    flex-direction: column;
  }
}
